import '../scss/main.scss'

window.onload = prepareButton;

function prepareButton() {
  document.getElementById('icon').onclick = function() {
    var ret = document.getElementById("icon");


    var element = document.getElementById("MyElement");
    element.classList.toggle("menu-active");


    var ico = document.getElementById("change").classList;



    if (ico.contains("fa-bars")) {

      ico.remove("fa-bars");

    } else {

      ico.add("fa-bars");
      document.getElementById("change").style.color = "#4b21ff";
    }

    if (ico.contains("fa-times")) {

      ico.remove("fa-times");

    } else {

      ico.add("fa-times");
      document.getElementById("change").style.color = "white";
    }


  }



}
